@media screen and (min-width: 769px){.uk-panel:hover .uk-overlay{-webkit-transform:translate(0, -5px);transform:translate(0, -5px)}}.uk-panel:hover .uk-overlay:not(.is-open){box-shadow:0 5px 11px rgba(0,0,0,0.2)}.uk-panel:hover .uk-overlay:not(.is-open) span{opacity:1;-webkit-transform:scale(1);transform:scale(1)}.uk-overlay{box-shadow:0 1px 4px rgba(0,0,0,0.15);width:100%;cursor:pointer;-webkit-transition:all 0.2s ease;transition:all 0.2s ease}.uk-overlay img{width:100%}.uk-overlay span{opacity:0;-webkit-transform:scale(0);transform:scale(0);-webkit-transition:all 0.2s ease;transition:all 0.2s ease}.uk-overlay-panel{background:rgba(255,255,255,0.9);padding:10px 10px 10px;color:#222}.uk-overlay-panel h3{font-weight:300}



