html{box-sizing:border-box}*,*:before,*:after{box-sizing:inherit}body{font-family:'Lato', sans-serif;font-size:18px;line-height:1.5;font-weight:300;color:#333}h1,h2,h3{font-family:'Montserrat', sans-serif}h4,h5,h6{font-family:inherit}a{color:#0088cc}.section-divider{border-color:#ccc;border-style:dashed}main{max-width:960px;margin:0 auto;padding:20px}.job-title{display:block;margin:0 0 15px;font-size:14px;font-weight:300;color:#888;text-transform:uppercase}.fade-transition{-webkit-transition:all .2s ease;transition:all .2s ease}.fade-leave,.fade-enter{opacity:0;-webkit-transform:translateY(5px);transform:translateY(5px)}



