fullscreen-project-overlay.project-jachin-capital a{color:#23273c}#fullscreen-project-overlay.project-jachin-capital svg>g>g{stroke:#23273c}#fullscreen-project-overlay.project-theedge:after{background:-webkit-linear-gradient(bottom, #e31e26, rgba(227,30,38,0));background:linear-gradient(to top, #e31e26, rgba(227,30,38,0))}#fullscreen-project-overlay.project-bigbloom{color:#000}#fullscreen-project-overlay.project-bigbloom:after{background:-webkit-linear-gradient(bottom, #fff, rgba(255,255,255,0));background:linear-gradient(to top, #fff, rgba(255,255,255,0))}#fullscreen-project-overlay.project-bigbloom a{color:#e9175a}#fullscreen-project-overlay.project-bigbloom svg>g>g{stroke:#e9175a}#fullscreen-project-overlay.project-vue-memory-game,#fullscreen-project-overlay.project-react-unsplash-client{color:#000}#fullscreen-project-overlay.project-vue-memory-game:after,#fullscreen-project-overlay.project-react-unsplash-client:after{background:-webkit-linear-gradient(bottom, #fff, rgba(255,255,255,0));background:linear-gradient(to top, #fff, rgba(255,255,255,0))}#fullscreen-project-overlay.project-vue-memory-game a,#fullscreen-project-overlay.project-react-unsplash-client a{color:#000}#fullscreen-project-overlay.project-vue-memory-game svg>g>g,#fullscreen-project-overlay.project-react-unsplash-client svg>g>g{stroke:#000}#fullscreen-project-overlay .close{position:absolute;top:20px;right:20px}@media (max-width: 768px){#fullscreen-project-overlay .close{position:fixed;top:auto;bottom:20px;right:20px;background:#fff;border-radius:50%;padding:10px;z-index:2;box-shadow:0 1px 4px rgba(0,0,0,0.35)}#fullscreen-project-overlay .close svg{display:block;width:40px;height:40px}#fullscreen-project-overlay .close svg>g>g{stroke:#3a4b60}}#fullscreen-project-overlay .overflow-container{overflow:auto;-webkit-overflow-scrolling:touch;height:100%}#fullscreen-project-overlay .project-details-container{box-sizing:border-box;padding:100px 25px 25px;margin:0 auto;max-width:768px}@media (max-width: 76